import React, { useState, useEffect } from "react";
import {makeStyles, withStyles} from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import ListItemText from "@material-ui/core/ListItemText";
import CancelIcon from "@material-ui/icons/Cancel";
import Select from "@material-ui/core/Select";
import Checkbox from "@material-ui/core/Checkbox";
import Chip from "@material-ui/core/Chip";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";

const useStyles = makeStyles((styleProp) => ({
  formControl: {
    width: (styleProp) => styleProp.width  || 271,
    minWidth: (styleProp) => styleProp.minWidth  || 120,
    maxWidth: (styleProp) => styleProp.maxWidth  || 300,
  },
  list: {
    backgroundColor: "green"
  },
  input: {
    backgroundColor: "blue",
    padding: "4px"
  },
  
  select: {
    backgroundColor: "red"
  },
  outlined: {
    padding: 0,
    paddingRight: 35,
    "&$focused": {
      border: 0
    }
  },
  iconOpen: {
  },
  root: {
    width: (styleProp) => styleProp.width  || 271,
    minWidth: (styleProp) => styleProp.minWidth  || 120,
    maxWidth: (styleProp) => styleProp.maxWidth  || 300,
    background: "#FFFFFF 0% 0% no-repeat padding-box !important",
    border: (styleProp) => styleProp.border || "1px solid #C4C4C4 !important",
    borderRadius: (styleProp) => styleProp.borderRadius || 2,
    opacity: 1,
    textAlign: (styleProp) => styleProp.textAlign || "start",
    maxHeight: (styleProp) => styleProp.maxHeight || 26,
    "& .MuiOutlinedInput-notchedOutline": {
      borderColor: "green"
    },
    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
      borderColor: "red"
    },
    "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
      border: 0
    },
    "& .MuiOutlinedInput-root": {
      "& fieldset": {
        border: "1px solid #C4C4C4 !important",
        borderRadius: "2px !important"
      },
      "&:hover fieldset": {
        border: "1px solid #C4C4C4 !important",
        borderRadius: "2px !important"
      },
      "&.Mui-focused fieldset": {
        border: "1px solid #C4C4C4 !important",
        borderRadius: "2px !important"
      },
      "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
        border: "1px solid #C4C4C4 !important"
      }
    },
    "& .MuiPaper-root":{
      top:"200px"
    }
  },
  chips: {
    display: "flex !important",
    flexWrap: "wrap !important"
  },
  chipRoot: {
    margin: 1
  },
  chip: {
    margin: 1,
    height: "20px",
    textAlign: "left",
    font: "normal normal normal 12px/20px Open Sans",
    letterSpacing: 0,
    maxWidth: '40%',
    color: "#000000",
    opacity: 1,
    borderRadius: 2,
    "& label": {
      paddingLeft: -8,
      paddingRight: -8
    }
  },
  chipLabel: {
    paddingLeft: 4,
    paddingRight: 4
  },
  chipSizeSmall: {
    margin: 4
  },
  listSubheaderRoot: {
    textAlign: "left",
    font: "normal normal 600 11px/20px Open Sans",
    letterSpacing: "0px",
    color: "#000000",
    opacity: 1
  },
  focused: {
    backgroundColor: "red",
    border: '0px !important'
  },
  notchedOutline: {
    border: '0px !important'
  }
}));

const CustomCheckbox = withStyles({
  root: {
    color: "#0072C6",
    "&$checked": {
      color: "#0072C6"
    }
  },
  icon: {
    width: 12,
    height: 12,
    color: "#0072C6",
    borderRadius: "2px",
    opacity: 1
  },
  checked: {}
})((props) => <Checkbox color="default" {...props} />);


const CustomListItemText = withStyles({
  primary: {
    textAlign: "left",
    font: "normal normal normal 12px/20px Open Sans",
    letterSpacing: "0px",
    color: "#000000",
    opacity: 1
  },
  gutters: {
    paddingLeft: "27px"
  }
})((props) => <ListItemText {...props} />);

const CustomMenuItem = withStyles({
  root: {
    paddingTop: "0px",
    paddingBottom: "0px",
    backgroundColor: "#0072C622 !important"
  },
  gutters: {
    paddingLeft: "17px"
  },
  selected: {
    backgroundColor: "rgba(0, 0, 0, 0.00) !important"
  }
})((props) => <MenuItem {...props} />);


export default function DropdownWithCheckbox(props) {

  const categories = {
    Control: props.dropdownOptions
  };

  const classes = useStyles({
    minWidth:props.minWidth,
    maxWidth: props.maxWidth,
    width: props.width,
    border: props.border
  });
  

  const [availableOptions, setAvailableOptions] = useState(null);

  var dynamicOptions = [];
  Object.keys(categories).forEach(function categoryFunc(categoryName, index) {

 
    categories[categoryName].forEach((optionName, optIndex) => {
      dynamicOptions.push(
        <CustomMenuItem key={optionName.value} value={optionName.value}>
          <CustomCheckbox
            checked={props.selectedOptions.includes(optionName.value)}
            color="blue"
            onClick={() => handleSelectedOption(optionName.value)}
          />
          <CustomListItemText
            primary={optionName.value}
            onClick={() => handleSelectedOption(optionName.value)}
          />
        </CustomMenuItem>
      );
    });
  });




  const handleChange = (event) => {

    
  };

  const handleDelete = (value) => {
    // e.preventDefault();
    // props.setSelectedOptions((current) => _without(current, value));
    props.setSelectedOptions(value);

  };

  useEffect(() => {
    if (props.onChangeHandler) props.onChangeHandler(props.selectedOptions);
    return () => {
    };
  }, [props.selectedOptions]);

  function handleSelectedOption(option_value) {
    props.setSelectedOptions(option_value);
  }

  return (
    <div>
      <div className={classes.redBackground}>
        <FormControl className={classes.formControl} variant="outlined">
          <Select
            labelId={props.id}
            id={props.id}
            multiple
            value={props.selectedOptions}
            onChange={handleChange}
            onOpen={() => console.log()}
            IconComponent={KeyboardArrowDownIcon}
            renderValue={(selected) => (
              <div className={classes.chips}>
                {selected.length>2?
                <>
                {selected.slice(0,2).map((value) => (
                  
                  <Chip
                    size="small"
                    key={value}
                    className={classes.chip}
                    label={value}
                    clickable
                    deleteIcon={
                      props.deleteIcon ? (
                        <props.deleteIcon
                        //  onMouseDown={(event) => event.stopPropagation()}
                       onClick={(e) => handleSelectedOption(value)}
                        />
                      ) : (
                        <CancelIcon
                          onMouseDown={(event) => event.stopPropagation()}
                        />
                      )
                    }
                   // onClick={(e) => handleSelectedOption(value)}
                    onDelete={(e) => handleDelete(value)}
                  />))}
                    <span
                      title={[...selected].slice(2,selected.length).join(", ")}
                      style={{
                        font: "normal normal normal var(--base_text_font_size)/1.75rem var(--font_family)",
                        color: "#606060",
                        cursor: "default",
                        marginLeft:'.5rem'
                      }}
                    >
                      +{selected.length - 2}
                    </span>
                    </>
                    :
                    selected.map((value) => (
                      <Chip
                        size="small"
                        key={value}
                        className={classes.chip}
                        label={value}
                        clickable
                        deleteIcon={
                          props.deleteIcon ? (
                            <props.deleteIcon
                            //  onMouseDown={(event) => event.stopPropagation()}
                        onClick={(e) => handleSelectedOption(value)}
                            />
                          ) : (
                            <CancelIcon
                              onMouseDown={(event) => event.stopPropagation()}
                            />
                          )
                        }
                       // onClick={(e) => handleSelectedOption(value)}
                        onDelete={(e) => handleDelete(value)}
                      />
                    )
                  )}
              </div>
            )}
            classes={{
              root: classes.root,
              outlined: classes.outlined,
              focused: classes.focused,
              iconOpen: classes.iconOpen,
              input: classes.input,
              list: classes.list,
              padding: classes.padding,
              sizeSmall: classes.sizeSmall,
              notchedOutline: classes.notchedOutline
            }}
          >
            {dynamicOptions}
          </Select>
        </FormControl>
      </div>
    </div>
  );
}
