import React,{ useContext, useEffect }  from 'react';
import { Row, Col, ButtonGroup} from 'react-bootstrap' ;
import Backdrop  from '@material-ui/core/Backdrop' ;
import {makeStyles} from '@material-ui/core/styles' ;
import Button from '@material-ui/core/Button' ;
//import styles from '../../../LeftNavigation/CustomControlBACKDROP/CustomControl.module.css' ;
import styles from './ConditionalVisibilityTab.module.css' ;
//import {AppContext} from '../../../../Resources/CanvasContextFile' ;
import RedCross from '../../../../Icons/RedCross';
import AddColumnIcon from '../../../../Icons/AddColumnIcon';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import DropdownWithCheckbox from './DropdownWithCheckbox' ;
import MappingFile from '../../../../ReusableComponents/Mapping/MappingFile';
import Visibility from '@material-ui/icons/Visibility';
import { Menu, MenuItem } from '@material-ui/core';
import axios from 'axios';
import secureLocalStorage from 'react-secure-storage';
import { useTranslation } from 'react-i18next';
import { useSelector } from 'react-redux';
import CustomizedAlert from '../../../../ReusableComponents/Alert/CustomizedAlert';


const useStyles = makeStyles((theme) => ({
  backdrop: {
    // width: '1047px',
    // height: '394px',
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

export default function ConditionalVisibilityTabs(props) {
 // const AppContextVar = useContext(AppContext);

  const {t}=useTranslation();
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [alertData, setAlertData] = React.useState({
    open: false,
    showConfirm: "showNotification",
  })
  const [Rules, setRules] =  React.useState({  });
  const [ruleSetDetails, setruleSetDetails] = React.useState([]);
  const [modified, setModified] = React.useState(false);
  const [mappingField, setMappingField] = React.useState(false);
  const [selectedRuleIndex, setSelectedRuleIndex] = React.useState(0);
  const [filteredCustomControlName, setFilteredCustomControlName] = React.useState({'customControlName' : '','customControlId' : ''});
  
  const [variablesName, setVariablesName] =  React.useState([ ]);
  const [displayedSheets, setDisplaySheets] = React.useState([]);
  const [sheetList, setSheetList] = React.useState([]);
  const [selectedOption, setSelectedOption] = React.useState((props.portalState==="Processes")?'Activity Name':"Data Variables");
  const [clickedOnce, setClickedOnce] = React.useState(false);
  const [isCreatingNew, setIsCreatingNew] = React.useState(false);
  const [openVisiblityMenuIndex, setOpenVisiblityMenuIndex] = React.useState(null);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [activityNamesOptions, setActivityNamesOptions] = React.useState([]);
  const translationStyles1=window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA";
  


  const OperatorArray = ["=",">","<",">=","<="];
  const OperatorArray2 = ["="];
  
  var formState;
  var controlSetJson = {  };  
  var initiallySelectedSheetlist = [  ] ;
  if(props.formState2!==undefined){
    if(props.formState2!==undefined && Object.keys(controlSetJson).length === 0){  
        formState = props.formState2 ;
        if(props.selectedControl && props.selectedControl.children){
            var sheetlist = [  ] ;
            
            props.selectedControl.children.map((child)=>{ 
                sheetlist.push({'Title' : child.tabChildName, 'value' : child.tabChildName}) ;
                initiallySelectedSheetlist.push(child.tabChildName) ;
            })
              
        }
        if(modified=== false){ 
            if(props.ruleDetails && props.ruleDetails.activityName && props.ruleDetails.dataVariables){
                setruleSetDetails(props.ruleDetails.activityName) ;
                setRules(props.ruleDetails);
            }
            else if(props.ruleDetails  && props.ruleDetails.activityName) {
                setSelectedOption("Activity Name")
                setruleSetDetails(props.ruleDetails.activityName) ;
                setRules((prev)=> {return {...props.ruleDetails, 
                    "dataVariables": [{'label':'Rule 1', 'variableName' : '', 'operator' : '', 'value' : '', 'displayedSheets' : initiallySelectedSheetlist, 'hiddenSheets' : [  ],isNew:true}]
                }});
            }
            else if(props.ruleDetails && props.ruleDetails.dataVariables){ 
                setruleSetDetails(props.ruleDetails.dataVariables) ;
                setRules((prev)=> {return {...props.ruleDetails, 
                    "activityName": [{'label':'Rule 1', 'variableName' : '', 'operator' : '', 'value' : '', 'displayedSheets' : initiallySelectedSheetlist, 'hiddenSheets' : [  ],isNew:true}]
                }});
                setSelectedOption("Data Variables")
            }
            else {
                setruleSetDetails([{'label':'Rule 1', 'variableName' : '', 'operator' : '', 'value' : '', 'displayedSheets' : initiallySelectedSheetlist, 'hiddenSheets' : [  ],isNew:true}]) ;
                setRules((prev)=> {return {
                    "activityName": [{'label':'Rule 1', 'variableName' : '', 'operator' : '', 'value' : '', 'displayedSheets' : initiallySelectedSheetlist, 'hiddenSheets' : [  ],isNew:true}],
                    "dataVariables": [{'label':'Rule 1', 'variableName' : '', 'operator' : '', 'value' : '', 'displayedSheets' : initiallySelectedSheetlist, 'hiddenSheets' : [  ],isNew:true}]
                }});
                
            }
            
            //if(formState.children && formState.children.length>=2 && formState.children)
            var List =  readFormData(formState.children, {'controlsList' : [  ], 'sheetList' : [  ]}) 
            setVariablesName(List.controlsList);
            setSheetList(sheetlist); 
            if(props.ruleDetails && props.ruleDetails.activityName && props.ruleDetails.activityName[0] && props.ruleDetails.activityName[0].displayedSheets){
                setDisplaySheets(props.ruleDetails.activityName[0].displayedSheets);
            }
            else{
                setDisplaySheets(initiallySelectedSheetlist);
            }
            setModified(true); 
        }
    }
  }
  useEffect(()=>{
   if(props.processId && props.statusType){
    // const launchpadKey=secureLocalStorage.getItem("launchpadKey");
    const jwtToken = JSON.parse(secureLocalStorage.getItem(window.isPortalLive ?  `launchpadKey_${window.applicationName}` || "launchpadKey" : "launchpadKey"))?.token ||
  undefined;
    // const jwtToken=JSON.parse(launchpadKey)?.token || undefined;
    axios
    .get(
      `/pmweb/activites?processId=${props.processId}&processType=${props.statusType}`,{headers:{
        Authorization:`${jwtToken}`
      }})
    .then((res) => {
    
    setActivityNamesOptions(res.data?.map(item=>item.actName));
    })
    .catch((err) => {
      console.log(err);
    });
   }
},[props.processId,props.statusType]) ;
    useEffect(()=>{
        if(ruleSetDetails[selectedRuleIndex] && ruleSetDetails[selectedRuleIndex].displayedSheets){
            setDisplaySheets([...ruleSetDetails[selectedRuleIndex].displayedSheets]);
        }
        else{
            setDisplaySheets(initiallySelectedSheetlist);
        }
    },[selectedRuleIndex]) ;
    useEffect(()=>{
        if(selectedOption === "Activity Name"){
            setRules((prev)=>{ return {...prev, 'activityName' : [...ruleSetDetails]}});
        }
        else{
            setRules((prev)=>{ return {...prev, 'dataVariables' :[...ruleSetDetails]}});
        }
    },[ruleSetDetails]) ;

    function handleChangeOption(arg) {
        if(arg === "Activity Name"){
            setRules((prev)=> {return {...prev, 
                "dataVariables": [...ruleSetDetails]
            }});
            setruleSetDetails(Rules.activityName);
            setDisplaySheets((Rules.activityName)[0].displayedSheets);
        }
        else if(arg === "Data Variables"){
            setRules((prev)=> {return {...prev, 
                "activityName": [...ruleSetDetails] 
            }});
            setruleSetDetails(Rules.dataVariables);
            setDisplaySheets((Rules.dataVariables)?.[0]?.displayedSheets);
        }
        setSelectedOption(arg) ;
        setSelectedRuleIndex(0)
        setClickedOnce(false)
    }

    function handleOption(arg){ 
        setAlertData({
            'open':true,
            'showConfirm':'showconformationDialogBox',
            'alertContentKey': arg === "Activity Name" ? 'DeleteDataVariables' : 'DeleteActivityRules',
            'alertTitleKey':arg === "Activity Name" ? 'DeleteDataVariablesTitle' : 'DeleteActivityRulesTitle', 
            "Button1Key" : 'No',
            "Button2Key" : 'Yes',
            "arg" : arg,
            "conditionalVisibility": true,
            // "controlId" : Id,
            // "data":props.data,
            // "evnt":ev
        })
    }
    
   
  const handleToggle = () => {
    setOpen(!open);
  };



  function saveRuleSet(arg){ 
      if(arg === 'Cancel'){  
        props.setShowConditionalVisibility() ;
        return;
      }
      var temp_rules = {};
      if(selectedOption === "Activity Name"){
        let flag =false;
        ruleSetDetails.map((child) => {
            if(child.value === '' || child.value === '--' || child.operator === '' || child.operator === '--')
            {
                flag = true;
            }
        })
        if(flag) return;
        temp_rules["activityName"] = [...ruleSetDetails] ;
      }
      else if(selectedOption === "Data Variables"){
        let flag = false;
        ruleSetDetails.map((child) => {
            if(child.value === '' || child.variableName === '' || child.operator === '' || child.operator === '--')
            {
                flag = true;
            }
        })
        if(flag) return;
        temp_rules["dataVariables"] = [...ruleSetDetails] ;
      }
      if(arg === 'Save'){
            
            props.selectedControl['RuleDetails'] = temp_rules ;
            props.updatedComponentInFormState(
                props.selectedControl.id,
                props.selectedControl
            );
            props.setCheckConditionalitySave();
      }
      props.setShowConditionalVisibility() ;
  }
  
  function filterSearchedItem(action,searchedText){   
    if(action === 'customControlName' ){   
        //   ruleSetDetails.map((controlSet)=>{
        //         if(controlSet.label && controlSet.label.includes(searchedText)){ 
                    setFilteredCustomControlName((prev)=> {return{...prev, 'customControlName' : searchedText}});
        //         } 
        //   });    
    }
    else if(action==='controlId'){ 
        setFilteredCustomControlName((prev)=> {return{...prev, 'controlId' : searchedText}});
    }
    else if(action==='controlName'){ 
        if(searchedText === 'horizontal'){ 
        
        }
    }
  }
 
    
  function handleControlSet(action,index,controlId){  
    if(action === 'create'){
    const ids = ruleSetDetails.map((rule) => +rule.label.split(" ")[1]);
    let maxId = Math.max(...ids);
    if (ids.length === 0) {
      maxId = 0;
    }
        setruleSetDetails(prev => [...prev, {'label' : 'Rule ' + (maxId + 1),'variableName' : '', 'operator' : '', 'value' : '', 'displayedSheets' : initiallySelectedSheetlist, 'hiddenSheets' : [  ],'isNew':true}]) ;
        setSelectedRuleIndex(ruleSetDetails.length) ;
        setClickedOnce(false)
        setDisplaySheets(initiallySelectedSheetlist);
    } 
    else if(action === 'selectedControlSet' && selectedRuleIndex!==index){
        setSelectedRuleIndex(index) ;
        setClickedOnce(false)
    }
    else if(action === 'changeLabel'){ 
        setruleSetDetails(
            ruleSetDetails.map((controlSet, setIndex)=>{ 
                if(setIndex === selectedRuleIndex )
                    return {...controlSet, 'label' : index}
                return controlSet
        }))
    } 
    else if(action === 'delete'){
        var controlSetBeforeDeletion = [...ruleSetDetails];
        controlSetBeforeDeletion.splice(index, 1); 
        if(controlSetBeforeDeletion.length>0){
        setSelectedRuleIndex(Math.min(selectedRuleIndex, controlSetBeforeDeletion.length-1));
    }else{
        setSelectedRuleIndex(-1);
    }
        setruleSetDetails(controlSetBeforeDeletion);
    }
    setClickedOnce(false)
    
  }

  
    function handleSelectedOptions(option){
        const index = displayedSheets.indexOf(option) ; 
        let temp_displayedSheets = [...displayedSheets] ;
        if(index>-1){
            temp_displayedSheets.splice(index,1); 
        }    
        else {
            temp_displayedSheets = [...temp_displayedSheets, option] ;
        }
        updateRuleSet('displayedSheets', [...temp_displayedSheets] ) ;
        setDisplaySheets([...temp_displayedSheets]) ;
        const hiddenSheets = [  ]  ;  
        sheetList.map((sheet)=>{
            if(!temp_displayedSheets.includes(sheet.value))
                hiddenSheets.push(sheet.value);
        })
        updateRuleSet('hiddenSheets', [...hiddenSheets] ) ;
        
    }
    function handleVariableName(id,value){  
        
        if(id==="variableName"){
         /*   var select = document.getElementById(id); 
            var optionTag = select.options[select.selectedIndex]; 
            
            updateRuleSet(id,optionTag.value);*/
            updateRuleSet(id,value);

        }
        else if(id==="operator"){
            var select = document.getElementById(id); 
            var optionTag = select.options[select.selectedIndex];   
          
            updateRuleSet(id,optionTag.value) ;
        }
        else if(id==="value" && selectedOption==='Activity Name'){
            
            const select = document.getElementById(id); 
            const valTag = select.options[select.selectedIndex]; 
            updateRuleSet(id,valTag.value);

        } else if(id==="value"){
         
            updateRuleSet(id,value);
        }
        else if(id==='AddingNew'){
            if(ruleSetDetails[selectedRuleIndex] && ruleSetDetails[selectedRuleIndex]?.isNew){
                const newRuleSetDetails=[...ruleSetDetails]
                 delete newRuleSetDetails[selectedRuleIndex].isNew;
                 setruleSetDetails([...newRuleSetDetails])
            }
        } else if(id==='Modify'){
            setClickedOnce(true)
            setAlertData({
                open: true,
                showConfirm: "showNotification",
                alertType: "success",
                alertContentKey: "Modify Successfully"
            });
        }
        
         
    }
    function updateRuleSet(property, value){  
        setruleSetDetails((prev)=> {return [...prev.map((ruleSet, ruleIndex)=>{ 
                if(ruleIndex === selectedRuleIndex ){ 
                    return {...ruleSet,[property] : value };
                }
                return ruleSet ;
        })]})
    }
    

     const selectedVariableCallback=(selVar)=>{
      
        updateRuleSet("variableName",selVar);
        setMappingField(false);

     }


     const handleClickVisiblity = (e,index) => {
       setOpenVisiblityMenuIndex(index);
       setAnchorEl(e.currentTarget)
     };
     useEffect(()=>{
        document.getElementById("backdropDiv")?.focus();
        
       },[])
     function handleEscapeKey(e){
        if(e.code === "Escape"){
            saveRuleSet('Cancel')
        } 
       }
     
   
     const handleCloseVisiblity = () => {
       setOpenVisiblityMenuIndex(null);
       setAnchorEl(null)
     };
    const getDisplayedSheetOption=()=>{
        if(ruleSetDetails[selectedRuleIndex] && ruleSetDetails[selectedRuleIndex].displayedSheets){
            return [...ruleSetDetails[selectedRuleIndex].displayedSheets];
        }
        else{
            return initiallySelectedSheetlist;
        }
    }
  return (
    <div>
      <Backdrop className={classes.backdrop} open={true} >
      <div tabIndex="0" onKeyDown={(e) => handleEscapeKey(e)} className={styles.backdropDiv} id="backdropDiv">
        <Row className={styles.backdropHeader}>
            {t('Conditional Visibility')}
        </Row>
        <Row className={styles.backdropBody}>
            
                <Col className={styles.conditionalVisibilityBackdropLeftBody}>
                   <Col className={styles.controlSetAddCol}>
                        {t('Rule Set')}
                        <img src= {require('../../../../Icons/controlSetAdd.svg').default} style={{'height' : '100%', 'cursor' : 'pointer'}} 
                        onClick={()=>handleControlSet('create')} alt='Control Set Add Icon'/>
                    </Col>
                    <Col className={styles.searchCol}>
                        <input aria-label={"Custom Control Name"} type='text' placeholder={'Search'} className={styles.searchInput} 
                        onChange={(event)=>filterSearchedItem('customControlName',event.target.value)} />
                        <img src= {require('../../../../Icons/Search.svg').default}  className={styles.searchIcon} style={translationStyles1?{right:"unset",left:"8px"}:null} alt='Search Icon' />
                    </Col>
                    <div className={styles.controlSetNameColParent}>
                    {  filteredCustomControlName && filteredCustomControlName['customControlName'].length === 0 ? 
                            ruleSetDetails && ruleSetDetails.map((controlSet, index)=>{
                            return(
                                <Col className={[styles.controlSetNameCol, selectedRuleIndex === index ? styles.selectedControlSet : null].join(' ')} 
                                onClick={()=>handleControlSet('selectedControlSet', index)}>
                                    <span className={styles.pageTitleText} onClick={()=>handleControlSet('selectedControlSet', index)}>{controlSet.label}</span>
                                    {selectedRuleIndex === index ?
                                    <>
                                      <span style={translationStyles1?{marginLeft:'-8rem',marginTop:'2px'}:{marginRight:'-8rem',marginTop:'2px'}}> 
                                        <Visibility style={{height:'20px',width:'20px'}}
                                        aria-controls="simple-menu" aria-haspopup="true" 
                                        onClick={(e)=>handleClickVisiblity(e,index)}/>
                                    </span>
                                    <span> 
                                        <img src= {require('../../../../Icons/deleteRed.svg').default} className={styles.deleteEditIcon}  
                                        onClick={()=>handleControlSet('delete', index)} alt='Delete Icon'/>
                                    </span>
                                    </>
                                    :
                                    <></>
                                    }
                                    
                                </Col>
                                )
                            })
                        :
                        ruleSetDetails && ruleSetDetails.map((controlSet, index)=>{
                            
                            return(
                                controlSet.label && controlSet.label.toLowerCase().includes(filteredCustomControlName['customControlName'].toLocaleLowerCase()) ?
                                <Col className={[styles.controlSetNameCol, selectedRuleIndex === index ? styles.selectedControlSet : null].join(' ')} 
                                onClick={()=>handleControlSet('selectedControlSet', index)}>
                                    <span className={styles.pageTitleText} onClick={()=>handleControlSet('selectedControlSet', index)}>{controlSet.label}</span>
                                    {selectedRuleIndex === index ?
                                    <span> 
                                        <img src= {require('../../../../Icons/deleteRed.svg').default} className={styles.deleteEditIcon}  
                                        onClick={()=>handleControlSet('delete', index)} alt='Delete Icon'/>
                                    </span>
                                    :
                                    <></>
                                    }
                                    
                                </Col>
                                :<></>
                                )
                        })
                    }
                    </div>
                    <Menu
                      id="simple-menu"
                    anchorEl={anchorEl}
                    keepMounted
                     open={selectedRuleIndex===openVisiblityMenuIndex}
                    onClose={handleCloseVisiblity}
                    >
                    {ruleSetDetails[selectedRuleIndex]?.hiddenSheets?.map(sheet=><MenuItem >{sheet}</MenuItem>)}
                                    
                    </Menu>
                </Col>
                {selectedRuleIndex>-1 && ruleSetDetails && ruleSetDetails.length>0&&
                <Col className={styles.conditionalVisibilityBackdropRightBody}>
                        <Col className={styles.conditionalVisibilityButtonsCol}>
                            {(props.portalState==='Processes')&&
                            <Button  disableFocusRipple
                            disableRipple
                            tabIndex={0}
                            className={[styles.activityNameButton,translationStyles1?styles.activityNameButton1:"",
                             selectedOption === "Activity Name" ? styles.selectedConditionalVisibilityOption : null, "formBuilder-MuiButton-outlined"].join(" ")}  aria-label="medium primary button group" 
                                onClick={(event)=>handleOption('Activity Name')}>
                                {t('Activity Name')}
                            </Button>}
                            <Button disableFocusRipple
                            disableRipple
                            tabIndex={0} className={[styles.dataVariablesButton,translationStyles1?styles.dataVariablesButton1:"",
                             selectedOption === "Data Variables" ? styles.selectedConditionalVisibilityOption : null, "formBuilder-MuiButton-outlined"].join(" ")} aria-label="medium secondary button group" 
                            onClick={(event)=>handleOption('Data Variables')}>
                                {t('Data Variables')}
                            </Button>
                        </Col>
                        
                    <Col className={styles.controlsSetDefinitionDetails}>
                    
                    <TableContainer component={Paper} className={styles.tableContainer}>
                        <Table  aria-label="simple table">
                            <TableHead>
                                <TableRow className={[ ].join(" ")}>
                                    <TableCell className={[styles.activityValueCommonCol,styles.activityCol].join(" ")}>
                                        { selectedOption === "Activity Name" ? t('Activity') : t('Select Variable') }
                                    </TableCell>
                                    <TableCell className={[styles.activityValueCommonCol,styles.operatorCol].join(" ")}>
                                        {t('Operator')}
                                    </TableCell>
                                    <TableCell className={[styles.activityValueCommonCol,styles.vslueCol].join(" ")}>
                                        {t('Value')}
                                    </TableCell>
                                    <TableCell className={[styles.activityValueCommonCol,styles.displayCol].join(" ")}>
                                        {t('Display')}
                                    </TableCell>
                                </TableRow >   
                            </TableHead>
                            <TableBody id={ props.currentPage } > 
                            
                                {/* {  ruleSetDetails && ruleSetDetails[selectedRuleIndex] && (ruleSetDetails[selectedRuleIndex]['properties']).map((propertyObj, index)=>{ 
                                        return(   
                                            <TableRow className={[ ].join(" ")}>
                                            <TableCell className={[styles.operatorValueCommonCell, styles.activityCol].join(" ")}>
                                                { selectedOption === "Activity Name" ? 
                                                    <input type="text" defaultValue={'Activity Name'} disabled className={styles.activityInput} />
                                                    : 
                                                    <select className={[styles.activityInput].join(" ")}  
                                                    id={'variableName'} 
                                                    onChange={()=> handleVariableName('variableName',index)}>
                                                    {variablesName.map((variable)=>{
                                                        return(
                                                            <option value={variable}>{variable}</option>
                                                        )
                                                        }) 
                                                    }
                                                    </select>
                                                }
                                            </TableCell>
                                            <TableCell className={[styles.operatorValueCommonCell, styles.operatorCol].join(" ")}>
                                                <select className={[styles.activityInput].join(" ")}
                                                id={'operator'} 
                                                onChange={()=> handleVariableName('operator',index)}>
                                                    <option value="=" > {"="} </option>
                                                    <option value=">" > {">"}</option>
                                                    <option value=">=" > {">="}</option>
                                                    <option value="<" > {"<"}</option>
                                                    <option value="<=" > {"<="}</option>
                                                </select>
                                            </TableCell>
                                            <TableCell className={[styles.operatorValueCommonCell, styles.valueCol].join(" ")}>
                                                <input type="text" defaultValue={''} className={styles.activityInput} 
                                                onBlur={(ev)=> handleVariableName("value",index,ev.target.value)}/>
                                            </TableCell>
                                            <TableCell className={[styles.operatorValueCommonCell, styles.displayCol].join(" ")}>
                                                <DropdownWithCheckbox 
                                                selectedOptions={displayedSheets}
                                                setSelectedOptions={(arg)=>handleSelectedOptions(arg,index)}
                                                dropdownOptions={sheetList}
                                                maxWidth={'200px'}
                                                width={'200px'}
                                                key={displayedSheets.length}
                                                />
                                            </TableCell>
                                            </TableRow >  
                                            
                                        )
                                    })  
                                }    */}
                                <TableRow className={[ ].join(" ")}>
                                <TableCell className={[styles.operatorValueCommonCell, styles.activityCol].join(" ")}>
                                    { selectedOption === "Activity Name" ? 
                                        <input aria-label={"Activity Name"} type="text" defaultValue={'Activity Name'} value='Activity Name' disabled className={styles.activityInput} />
                                        : 
                                        <input aria-label={"Control Value1"}type="text" className={styles.activityInput} 
                                        value={selectedOption!=='Activity Name'&&ruleSetDetails[selectedRuleIndex] && ruleSetDetails[selectedRuleIndex].variableName||""}
                                        onClick={()=>{setMappingField(true)}}

                                        />

                                       /* <select className={[styles.activityInput].join(" ")}
                                        value={ruleSetDetails[selectedRuleIndex] && ruleSetDetails[selectedRuleIndex].variableName}  
                                        id={'variableName'} onClick={()=>{setMappingField(true)}} >
                                            {ruleSetDetails[selectedRuleIndex] && ruleSetDetails[selectedRuleIndex].variableName &&<option value={ruleSetDetails[selectedRuleIndex] && ruleSetDetails[selectedRuleIndex].variableName} selected>{ruleSetDetails[selectedRuleIndex] && ruleSetDetails[selectedRuleIndex].variableName}</option>}
                            </select>*/
                                    }
                                </TableCell>
                                <TableCell className={[styles.operatorValueCommonCell, styles.operatorCol].join(" ")}>
                                    <select className={[styles.activityInput].join(" ")}  
                                    id={'operator'} 
                                    onChange={()=> handleVariableName('operator')}>
                                        <option value={'--'}>{}</option>
                                        { (selectedOption === "Activity Name" ? OperatorArray2 : OperatorArray)?.map((operator)=>{
                                            if(ruleSetDetails?.[selectedRuleIndex] && ruleSetDetails?.[selectedRuleIndex]?.operator === operator)
                                                return(
                                                    <option value={operator} selected>{operator}</option>
                                                )
                                            else
                                                return(
                                                    <option value={operator}>{operator}</option>
                                                )
                                            }) 
                                        }
                                    </select>
                                </TableCell>
                                <TableCell className={[styles.operatorValueCommonCell, styles.valueCol].join(" ")}>
                                { selectedOption === "Activity Name" ? 
                                      
                                        <select className={[styles.activityInput].join(" ")}  
                                        id={'value'} 
                                            
                                        onChange={()=> handleVariableName('value')}>
                                        <option value={'--'}>{}</option>
                                        {activityNamesOptions.map((actName)=>{
                                            if(ruleSetDetails[selectedRuleIndex] && ruleSetDetails[selectedRuleIndex].value=== actName)
                                                return(
                                                    <option value={actName} selected>{actName}</option>
                                                )
                                            else
                                                return(
                                                    <option value={actName}>{actName}</option>
                                                )
                                            }) 
                                        }
                                        </select>:
                                    
                                    <input aria-label={"Control Value3"} type="text" className={styles.activityInput} key={ruleSetDetails?.[selectedRuleIndex] && ruleSetDetails?.[selectedRuleIndex]?.value}
                                        defaultValue={ruleSetDetails?.[selectedRuleIndex] && ruleSetDetails?.[selectedRuleIndex]?.value || ''} 
                                        onBlur={(ev)=> handleVariableName("value",ev.target.value)}/>}
                                </TableCell>
                                <TableCell className={[styles.operatorValueCommonCell, styles.displayCol].join(" ")} key={displayedSheets?.length + selectedRuleIndex}>
                                    <DropdownWithCheckbox 
                                    selectedOptions={displayedSheets}
                                    setSelectedOptions={(arg)=>handleSelectedOptions(arg)}
                                    dropdownOptions={sheetList}
                                    maxWidth={'200px'}
                                    width={'200px'}
                                    key={displayedSheets?.length}
                                    />
                                </TableCell>
                                </TableRow >  
                                    
                            </TableBody>
                        </Table>
                    </TableContainer>
                    {/* selectedOption === "Data Variables" ?*/}
                         <Row className={[styles.addButtonRow].join(" ")}>
                             {ruleSetDetails?.[selectedRuleIndex]&&<Button  className={[styles.addButton].join(" ")}  
                                 onClick={(event)=>handleVariableName(ruleSetDetails?.[selectedRuleIndex].isNew?'AddingNew':"Modify")}
                                // disabled={clickedOnce}
                                >
                                 {/* {ruleSetDetails[selectedRuleIndex].isNew?t('Add'):t('Modify')} */}
                                 {t('Modify')}
                             </Button>}
                         </Row > 
                    
                     {/*   :
                        null*/
                    }
                    </Col>
                </Col>}
        </Row>
        <Row className={styles.backdropFooter} style={translationStyles1?{paddingLeft:"10px"}:null}>
            <Button disableFocusRipple
                            disableRipple
                            tabIndex={0} className={`${styles.cancelButton} formBuilder-MuiButton-outlined`} onClick={()=>saveRuleSet('Cancel')}> {t('Cancel')} </Button>
            <Button disableFocusRipple
                            disableRipple
                            tabIndex={0} className={`${styles.saveButton} formBuilder-MuiButton-outlined`}
                            onClick={()=>saveRuleSet('Save')}> {t('Save')} </Button> 
        </Row>
        {mappingField ? (
              <MappingFile
                stateOpen={true}
                setMappingField={setMappingField}
                controlData={props.selectedControl}
                elementType={'textbox'}
                selectedVariableCallback={selectedVariableCallback}

              />
        ) : null}
      </div>
      </Backdrop>
      {
            alertData.open === true &&
            <CustomizedAlert
              alertData={alertData}
              handleFunction={(arg)=>handleChangeOption(arg)}
              // setOpenLoader={setOpenLoader}
            />
          }
    </div>
  );


  function readFormData(jsonData, List){
       
    if(Array.isArray(jsonData)){
        jsonData.forEach((item,index)=>{  
            if(item["elem_type"] && (item["elem_type"]==='textbox' || item["elem_type"]==='textarea' || item["elem_type"]==='multiselect')){ 
                if(item.Label){
                    List.controlsList.push(item.Label) ;
                }
                else if (item.style && item.style.DatePickerLabel){
                    List.controlsList.push(item.style.DatePickerLabel) ;
                }
                else if (item.style && item.style.Label){
                    List.controlsList.push(item.style.Label) ;
                } 
                else if (item.tabChildName){
                    List.controlsList.push(item.tabChildName) ;
                }
                else{
                    List.controlsList.push(item.id) ;
                } 
                // if( item["elem_type"]=='sheet'){
                //         if (item.tabChildName){
                //             List.sheetList.push({'Title' : item.tabChildName, 'value' : item.tabChildName}) ;
                //         } 
                //         else{
                //             List.sheetList.push({'Title' : item.id, 'value' : item.id}) ;
                //         } 
                // }
            
            }
            if(Object.keys(item).includes("children")){ 
                readFormData(item["children"], List);
            }
        })
    } else{
            if(jsonData["elem_type"] && (jsonData["elem_type"]==='textbox' || jsonData["elem_type"]==='textarea' || jsonData["elem_type"]==='multiselect') ){ 
                if(jsonData.Label){
                    List.controlsList.push(jsonData.Label) ;
                }
                else if (jsonData.style && jsonData.style.DatePickerLabel){
                    List.controlsList.push(jsonData.style.DatePickerLabel) ;
                }
                else if (jsonData.style && jsonData.style.Label){
                    List.controlsList.push(jsonData.style.Label) ;
                } 
                else if (jsonData.tabChildName){
                    List.controlsList.push(jsonData.tabChildName) ;
                }
                else{
                    List.controlsList.push(jsonData.id) ;
                }
                // if( jsonData["elem_type"]=='sheet'){
                //     if (jsonData.tabChildName){
                //         List.sheetList.push({'Title' : jsonData.tabChildName, 'value' : jsonData.tabChildName}) ;
                //     } 
                //     else{
                //         List.sheetList.push({'Title' : jsonData.tabChildName, 'value' : jsonData.tabChildName}) ;
                //     } 
                // } 
            }
            if(Object.keys(jsonData).includes("children")){
                readFormData(jsonData["children"],  List);
            }
            
    } return List ;
}

  
    
  
}