import React from 'react'
import "./ToggleBtn.css"

function ToggleBtn(props) {

    function handleToggle(e){
        const value=e.target.value;
       props.handleStyleChange(value);
    }

    return (
        <div className="slideBar">
               <label aria-label='Show Progress3'  className="switch" >
               {props.toggleState==="true"?
               <input aria-label={"Show Progress"} type="checkbox"
               checked
              name="showProgress"
                onClick={handleToggle}
                value="false"
              />
              :
              <input aria-label={"Show Progress2"} type="checkbox"
              name="showProgress"
              value="true"
              onClick={handleToggle}
              />
               }
               <span className="slider round"></span>
               </label>
         </div>
    )
}

export default ToggleBtn
