import React, { useState, useEffect, useCallback } from 'react'
import { Card } from 'react-bootstrap'
import ControlsMap from '../../../Properties/PropertiesConfiguration'
import TabsPropertiesJson from "../../../Properties/TabsPropertiesJson";
import axios from '../../../../axios';
import { debounce } from "lodash";
import CollapsibleComponent from '../../../../ReusableComponents/CollapseCard/CollapseCard';
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css"
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker";
import ToggleBtn from '../../../../ReusableComponents/ToggleBtn/ToggleBtn';
import ToggleBtn2 from '../../../../ReusableComponents/ToggleBtn/ToggleBtn2';
import { updateSheetId } from '../../IFormDesignerLogic';
import ImageUploadBtn from "../../../../ReusableComponents/ImageUploadBtn/ImageUploadBtn";
import TabStyle1 from "../../../../Icons/TabStyle1";
import TabStyle2 from "../../../../Icons/TabStyle2";
import TabStyle3 from "../../../../Icons/TabStyle3";
import TabStyle4 from "../../../../Icons/TabStyle4";
import RedCross from "../../../../Icons/RedCross";
import ControlLabel from '../../../../ReusableComponents/ControlLabel/ControlLabel';
import { getFormId, readOnlyChange, sendRequestForControl, updateFormStateJson} from '../../IFormDesignerLogic'
import ActiveIcon from "../../../../ReusableComponents/ActiveIcon/ActiveIcon";
import {regexValidator} from '../../../../Resources/globalFunctions';
import { useTranslation } from 'react-i18next';
import CustomizedAlert from '../../../../ReusableComponents/Alert/CustomizedAlert'
import ConditionalVisibilityTabs from './ConditionalVisibilityTabs';
import Svgdrag from "../../../../Icons/SectionIcons/drag";
import { FontWeightListJson } from "../../../Properties/FontWeightList";
import { createUniqueId } from '../../IFormDesignerLogic';
import { TabPropertyBackendJSON } from "./TabPropertyBackendJSON"
import PlusBlue from '../../../../Icons/PlusBlue';
import { functions } from '../../ApplicationAPI/applicationApi';
import { GetStateFromData } from '../../../CustomHooks/CustomHooks';

const TabsProperties = (props) => {
const contexts = GetStateFromData(props.containerId);
    const [topPx, setTopPx] = useState();
	  const {updateJson, 
      formState2, deleteControl,
      setOpenLoader, showAlertConfirmNotifications,processId,microFrontendData,
      statusType, openCanvasForSubform, isPortal  }=contexts.contexts;
	  const { updatedComponentInFormState,formMataData} =updateJson;

   
    const { t } = useTranslation(['styles', 'translation']);
    
    const style=props.selectedControl.style;
    const [alertData, setAlertData] = useState({
      open: false,
      showConfirm: "showNotification",
    });
    const [isButtonHover, setIsButtonHover] = useState(false);
    const [showConditionalVisibility, setShowConditionalVisibility] = useState(false);
    const [checkConditionalitySave, setCheckConditionalitySave] = useState(false);
    const [tempSelControl, setTempSelControl]= useState(props.selectedControl);
    const translationStyles1=window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA";
   

    useEffect(() => {
      if(checkConditionalitySave) {
        setAlertData({
          open: true,
          showConfirm: "showNotification",
          alertType: "success",
          alertContentKey: "Save Successfully"
      });
        setCheckConditionalitySave(false);
      }
    },[checkConditionalitySave])

    useEffect(() => {
      if(props.selectedControl)
        setTempSelControl(props.selectedControl);
    },[JSON.stringify(props.selectedControl?.children?.length)])

    var totalSheets = props.selectedControl.children.length;

    const addSheet = (tempSelControl) => {
        if(tempSelControl.children.length <= 9) {
          var controlData = {...tempSelControl};
          var frameIndex = tempSelControl && tempSelControl.frameIndex;
          var controlType = 'sheet';
          var extraDetails={};
          if(tempSelControl && tempSelControl.elementIndex){
              extraDetails.elementIndex = tempSelControl.elementIndex;
          }
          const tabControl = document.getElementById(tempSelControl?.id);
          if(tabControl) {
            extraDetails.insideMenu = tabControl.closest(`[elem_type=Menu]`) ? true : false;
            if(extraDetails.insideMenu) {
              const activeJsonString = document.querySelector(".menuMain_Id").getAttribute("activeJson");
              const activeJson = activeJsonString ? JSON.parse((activeJsonString || null)) : {};
        
              extraDetails.stepIndex = (activeJson.MainMenuIndex || activeJson.MainMenuIndex === 0) ? activeJson.MainMenuIndex : "-1"
              extraDetails.substepIndex = (activeJson.SubMenuIndex  || activeJson.SubMenuIndex === 0) ? activeJson.SubMenuIndex : "-1"
              extraDetails.subChildMenuIndex = (activeJson.subChildMenuIndex  || activeJson.subChildMenuIndex === 0) ? activeJson.subChildMenuIndex : "-1"
            }
            extraDetails.isSubForm = openCanvasForSubform === "subform" ? true: false
            extraDetails.isAdvanceListview = (openCanvasForSubform === "advanceListviewFromScratch") ? true : false
          }

          sendRequestForControl( frameIndex, 'add', controlType, extraDetails)
          .then((response)=>{
              var newSheet = response.data;
              let ColumnLayout = newSheet.ColumnLayout;
              if (ColumnLayout !== null && ColumnLayout !== undefined && ColumnLayout !== "") {
                try {
                  const MaxIteration = 13;
                  ColumnLayout = parseInt(ColumnLayout);
                  var rowElem = {};
                  rowElem.id = createUniqueId("row");
                  rowElem.children = [];
                  rowElem.elem_type = "row";
                  rowElem.frameIndex = controlData.children[0].children[0].frameIndex;
                  rowElem.parent_id = controlData.children[0].children[0].parent_id;

                  if(ColumnLayout > MaxIteration) ColumnLayout = MaxIteration;
                  if(ColumnLayout < MaxIteration) {
                    for (let clm = 0; clm < ColumnLayout;clm++) {
                      var col = {};
                      col.id = createUniqueId("column"); //newElement;
                      col.elem_type = "column";
                      const w = 12 / ColumnLayout;
                      col.w = 4;
                      col.md = w;
                      col.sm = 6;
                      col.xs = 12;
                      col.children = [];
                      col.frameIndex = controlData.children[0].children[0].frameIndex;
                      rowElem.children.push(col);
                    }
                  }
                  if (newSheet.children)
                    newSheet.children.push(rowElem);
                  else {
                    newSheet.children = [];
                    newSheet.children.push(rowElem);
                  }
                } catch (error) {
                    console.log(error);
                }
              }
      
              var controlId = controlData.id;
              if (controlData.children !== undefined && controlData.children !== null) {
                  controlData.children.push(newSheet);
      
              } else {
                  controlData.children = [];
                  controlData.children.push(newSheet);
              }
              updatedComponentInFormState(controlId, controlData);
              setTempSelControl(controlData);
              setOpenLoader(false);
          })
          .catch(err =>{
            setOpenLoader(false);
          })
        }
        else {
          showAlertConfirmNotifications({
            'open':true,
            'showConfirm':'showNotification',
            'alertType':'error',
            'alertContentKey':`${t('Max_10_Sheets_Allowed')}`
          });
        }
    }

    const handleDoubleClick = useCallback(debounce((data) => {
      addSheet(data);
    }, 200, {leading: true, trailing: false, maxWait: 200}), []);

    function confirmDeletion(sheetId,sheetIndex){
        setAlertData({...ControlsMap["customizedAlertConfirmationData"], 'open' : true, 'Id' :sheetId, 'sheetIndex': sheetIndex});
    }
    
    function deleteSheet(sheetId,sheetInd) {
        const frameIndex = tempSelControl && tempSelControl.frameIndex;
        const elementIndex = tempSelControl && tempSelControl.elementIndex;

        const extraDetails = {
          sheetId: sheetInd,
          frameIndex: frameIndex,
          elementIndex: elementIndex,
          isSubForm: openCanvasForSubform === "subform" ? true: false,
          isAdvanceListview: (openCanvasForSubform === "advanceListviewFromScratch") ? true : false,
          id: alertData?.Id
        };
        let controlData = tempSelControl;
        if (tempSelControl.children.length > 1) {
        sendRequestForControl( frameIndex, 'delete', "tabSheet123", extraDetails)
        .then((response) => {
          if(response.data.status === 0){
              controlData.children.splice(sheetInd,1);
              for(let child of controlData.children) {
                if(sheetInd < child.sheetIndex)
                  updateSheetId(child,child.sheetIndex - 1); 
              }
              updatedComponentInFormState(controlData.id,controlData);
              setTempSelControl(controlData);
              setOpenLoader(false);
          }
          else {
            showAlertConfirmNotifications({
              open: true,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentMsg: "Deleting control failed",
              autoHideDuration: 6000,
            });
          }
          
        })
        .catch((err)=> {
          setOpenLoader(false);
          showAlertConfirmNotifications({
            'open':true,
            'showConfirm':'showNotification',
            'alertType':'error',
            'alertContentKey': `${t('Unable_to_delete_Tab_Sheet')}`
          });
        })
        } else {
          //customalert
          showAlertConfirmNotifications({
            'open':true,
            'showConfirm':'showNotification',
            'alertType':'error',
            'alertContentKey':'CANT_DEL_LAST_TABCHILD'
          });
        }
    }

    let previousPosition;
    let newPosition;

    function allowDrop(event) {
      newPosition = event.target.attributes?.option_index?.value;
      let isParent = event.target.attributes?.option_parent;
      if (isParent) {
        event.target.classList.add("addBorder");
      }
      if (newPosition) newPosition = parseInt(newPosition);
      event.preventDefault();
    }

    function dragLeave(event) {
      let isParent = event.target.attributes?.option_parent;
      if (isParent) {
        if (event.target.classList.contains("addBorder")) {
          event.target.classList.remove("addBorder");
        }
      }
    }

    function registerOptionToReposition(event, optionIndex) {
      if (optionIndex || optionIndex === 0) previousPosition = parseInt(optionIndex);
    }

    function repositionThisOption(event,frameIndex,elementIndex) {
      if (event.target.attributes?.option_index?.value) {
        newPosition = parseInt(event.target.attributes.option_index.value);
        changeOptionIndex(frameIndex,elementIndex);
      }
      let isParent = event.target.attributes?.option_parent;
      if (isParent) {
        if (event.target.classList.contains("addBorder")) {
          event.target.classList.remove("addBorder");
        }
      }
      event.preventDefault();
    }

    const changeOptionIndex = (frameIndex, elementIndex) => {
      if (
        props.selectedControl.children &&
        previousPosition !== undefined &&
        newPosition !== undefined &&
        previousPosition !== null &&
        newPosition !== null &&
        typeof previousPosition === "number" &&
        typeof newPosition === "number"
      ) {
        let formId = getFormId()
        const jsonToSend = {
          frameIndex,
          elementIndex,
          oldPosition: previousPosition,
          newPosition,
          isSubForm: openCanvasForSubform === "subform" ? true: false,
          isAdvanceListview: (openCanvasForSubform === "advanceListviewFromScratch") ? true : false,
          formId: formId.toString()
        }

        axios.post("/idesign/reorderTabSheetPosition", jsonToSend, {withCredentials: true}).then((res) => {
          if(res.data.status === "0") {
            let children = [...tempSelControl.children];
            let tempElement = children[previousPosition];
            children[previousPosition] = children[newPosition];
            children[newPosition] = tempElement; 
            let temp = children[previousPosition].sheetIndex;
            children[previousPosition].sheetIndex = children[newPosition].sheetIndex;
            children[newPosition].sheetIndex = temp;
            updateSheetId(children[previousPosition],children[previousPosition].sheetIndex);
            updateSheetId(children[newPosition],children[newPosition].sheetIndex);
            let updatedControl = { ...props.selectedControl };
            updatedControl.children = children;

            updatedComponentInFormState(props.selectedControl.id, updatedControl);
            setTempSelControl(updatedControl);  
          }
          else if(res.data.status === "-1")
          {
            showAlertConfirmNotifications({
              'open':true,
              'showConfirm':'showNotification',
              'alertType':'error',
              'alertContentKey':`${t('Something_Went_Wrong')}`
            });
          }
        })
        .catch((err) => {
          showAlertConfirmNotifications({
            'open':true,
            'showConfirm':'showNotification',
            'alertType':'error',
            'alertContentKey':`${t('Something_Went_Wrong')}`
          });
        })
      } else {
        showAlertConfirmNotifications({
          'open':true,
          'showConfirm':'showNotification',
          'alertType':'error',
          'alertContentKey':`${t('Something_Went_Wrong')}`
        });
      }
    }

    var prevValue = '';
    function updateSheetName(sheetId, event) {
        var updatedName = event.target.value;
        if (updatedName !== prevValue && updatedName !== '') {
            prevValue = updatedName;
            let valid = true;
            if(ControlsMap["label"] && ControlsMap["label"]["regexType"]){
                let match = true;
                try{
                    match = regexValidator(updatedName, ControlsMap["label"]["regexType"]);
                }catch(error){
                     console.log(error);
                }
               if( match !== true){
                    valid = false;
                    if(updatedName.trim().length>0){
                      //customalert
                      showAlertConfirmNotifications({
                        'open':true,
                        'showConfirm':'showNotification',
                        'alertType':'error',
                        'alertContentKey':'NON_PERMISSIBLE'
                      });
                    }
                      
                }
            } 
            if(valid){
                props.selectedControl.children.forEach(
                    function (entry) {
                        if (entry.id === sheetId) {
                            entry.tabChildName = updatedName;
                        }
                    }
                );
            }
            updatedComponentInFormState(props.selectedControl.id, {...props.selectedControl});
        }
    }

    const [jsonIcons, setjsonForIcons] = useState({});

    function handleIcon(iconName, iconFile, uploadindex){
        const temp_style = style;
        temp_style["icon"]={"name":"", "file":""};
        temp_style["icon"]["name"] = iconName;
        temp_style["icon"]["file"] = iconFile;
        var jIcons={};
        if(jsonIcons!=undefined){
            jIcons = jsonIcons;
        } else {
            jIcons = {};
        }
        setjsonForIcons(jIcons);
        props.selectedControl.children[parseInt(uploadindex)].icon.name = iconName;
        props.selectedControl.children[parseInt(uploadindex)].icon.file = iconFile;
        updatedComponentInFormState(props.selectedControl.id , {...props.selectedControl})
    }


    var htmlArray = [];


if(props.selectedControl.style && props.selectedControl.style['AddIcon']===undefined)
{
    Object.values(props.selectedControl.children).map((key,index)=>{
        // if(key.icon)
        if(key.icon.file!=='')
        {
            props.selectedControl.style['AddIcon']="true";
        }
    })
}  
    
    function setToggleValue(value) {
        props.selectedControl.style['AddIcon']=value;
        if(value==="false")
        {
            Object.values(props.selectedControl.children).map((key,index)=>{
                if(jsonIcons[key.id]!==undefined)
                {
                    props.selectedControl.children[index]["icon"]["name"] = "";
                    props.selectedControl.children[index]["icon"]["file"] = "";
                }
            })
        }else{
            Object.values(props.selectedControl.children).map((key,index)=>{
                if(jsonIcons[key.id]!==undefined)
                {
                    props.selectedControl.children[index]["icon"]["name"] = jsonIcons[key.id].name;
                    props.selectedControl.children[index]["icon"]["file"] = jsonIcons[key.id].file;
                }
            })
        }
        updatedComponentInFormState(props.selectedControl.id, props.selectedControl)
    }
    htmlArray.push(<div className={styles.tabLabels}>
        <div  className={styles.tabAddIconPlacement} > <span className={styles.AddIconsLabelSpan}>{t(`translation:Add Icons`)}</span> 
          <ToggleBtn2 
            handleStyleChange={(value)=>setToggleValue(value)} 
            toggleState={props.selectedControl.style['AddIcon']}
          /> 
        </div>
        <div >
            {tempSelControl.children.map((key, ind) => {
                return (
                  <div
                    className="noLeftPadding flexDiv tablabelvalue"
                    key={ind}
                    option_index={ind}
                    draggable={true}
                    onDragStart={(event) =>
                      registerOptionToReposition(event, ind)
                    }
                    onDragOver={(event) => allowDrop(event)}
                    onDragLeave={(event) => dragLeave(event)}
                    onDrop={(event) => repositionThisOption(event, props.selectedControl?.frameIndex,props.selectedControl?.elementIndex)}
                    option_parent={"true"}
                  >
                    <div className="dragDiv dragDivPadded" option_index={ind}>
                      <Svgdrag className={"tabDragIconDot6"} color={"#afafaf"} />
                    </div>
                    <div
                      className={
                        props.selectedControl.style["AddIcon"] === "true"
                          ? styles.InputSheetBackgroundAfterAddIcon
                          : styles.InputSheetBackground
                      }
                    >
                      <input
                        aria-label={"Control Value1"}
                        id={key.id + "SheetNameInput"}
                        size={totalSheets > 1 ? "37" : "41"}
                        className={styles.TabLabelsInput}
                        key={key.id}
                        type="text"
                        defaultValue={key.tabChildName}
                        maxLength={window?.depsatkur_configs?.label_max_length}
                        onBlur={(event) => updateSheetName(key.id, event)}
                      />
                      {totalSheets > 1 ? (
                        <span
                          className={styles.deleteIconForTabSheetInput}
                          style={translationStyles1?{left:"0.3em",right:"unset"}:null}
                          onClick={() => confirmDeletion(key.id,key.sheetIndex)}
                        >
                          <RedCross />
                        </span>
                      ) : null}
                      {props.selectedControl.style["AddIcon"] === "true" && (
                        <div
                          className={styles.TabIconUploadBtn}
                          key={ind}
                          sheetindex={ind}
                        >
                          <ImageUploadBtn
                  containerId={props.containerId}
                            showIcon="true"
                            handleImageUpload={handleIcon}
                            iconName={
                              props.selectedControl.children[ind]["icon"][
                                "name"
                              ]
                            }
                            uploadindex={ind}
                          />
                        </div>
                      )}
                    </div>
                  </div>
                );
            })}
        </div>

        <div style={translationStyles1?{paddingLeft:"10px"}:null}>
            <button className={styles.AddTabButton} id="addButtonCommonStyle" onClick={() => handleDoubleClick(tempSelControl)} onMouseOver={() => setIsButtonHover(true)} onMouseLeave={() => setIsButtonHover(false)}><PlusBlue color={isButtonHover ? "#FFFFFF" : "#0072c6"}/> &nbsp; &nbsp; {t(`translation:Add Tab`)}</button></div>
    </div>
    );

    var CollapsibleItems = [];

    CollapsibleItems.push(<CollapsibleComponent id={'tabsLabels' + CollapsibleItems.length} heading="Tabs Labels" body={htmlArray} setTopPx={setTopPx}/>);

    var htmlArray2 = [];

    function selectedOptionForStyles(value)
    {
        if(value==='Style1')
        {            
            if(props.selectedControl.style["ActiveTabFontColor"]==="")
            {
                props.selectedControl.style["ActiveTabFontColor"]="#0072c6";  
            }
            if(props.selectedControl.style["ActiveTabBackColor"]==="")
            {
                props.selectedControl.style["ActiveTabBackColor"]="#FFFFFF";
            }
            if(props.selectedControl.style["ActiveTabFontWeight"]==="")
            {
            props.selectedControl.style["ActiveTabFontWeight"]="500";
            }
            if(props.selectedControl.style["ActiveTabFontSize"]==="")
            {
            props.selectedControl.style["ActiveTabFontSize"]="14px";
            }
            if(props.selectedControl.style["ActiveTabFontFamily"]==="")
            {
            props.selectedControl.style["ActiveTabFontFamily"]="Open Sans";
            }
            if(props.selectedControl.style["InActiveTabFontFamily"]==="")
            {
            props.selectedControl.style["InActiveTabFontFamily"]="Open Sans";
            }
            if(props.selectedControl.style["InActiveTabFontWeight"]==="")
            {
            props.selectedControl.style["InActiveTabFontWeight"]="500";
            }
            if(props.selectedControl.style["InActiveTabFontSize"])
            {
            props.selectedControl.style["InActiveTabFontSize"]="14px";
            }
            if(props.selectedControl.style["InActiveTabFontColor"]==="")
            {
                props.selectedControl.style["InActiveTabFontColor"]="#606060"; 
            }
            if(props.selectedControl.style["InActiveTabBackColor"]==="")
            {
                props.selectedControl.style["InActiveTabBackColor"]="#FFFFFF";
            }       
        }
        else if(value==='Style2')
        { 
            if(props.selectedControl.style["ActiveTabFontColor"]==="")
            {
            props.selectedControl.style["ActiveTabFontColor"]="#0072c6";
            }
            if(props.selectedControl.style["ActiveTabBackColor"]==="")
            {
            props.selectedControl.style["ActiveTabBackColor"]="#FFFFFF";
            }

            if(props.selectedControl.style["ActiveTabFontWeight"]==="")
            {
            props.selectedControl.style["ActiveTabFontWeight"]="500";
            }
            if(props.selectedControl.style["ActiveTabFontSize"]==="")
            {
            props.selectedControl.style["ActiveTabFontSize"]="14px";
            }
            if(props.selectedControl.style["ActiveTabFontFamily"]==="")
            {
            props.selectedControl.style["ActiveTabFontFamily"]="Open Sans";
            }    
            
            if(props.selectedControl.style["InActiveTabBackColor"]==="")
            {
              props.selectedControl.style["InActiveTabBackColor"]="#F8F8F8";
            }
            if(props.selectedControl.style["InActiveTabFontColor"]==="")
            {
              props.selectedControl.style["InActiveTabFontColor"]="#606060";
            }
            
            if(props.selectedControl.style["InActiveTabFontFamily"]==="")
            {
              props.selectedControl.style["InActiveTabFontFamily"]="Open Sans";
            }
            if(props.selectedControl.style["InActiveTabFontWeight"]==="")
            {
              props.selectedControl.style["InActiveTabFontWeight"]="500";
            }
            if(props.selectedControl.style["InActiveTabFontSize"]==="")
            {
              props.selectedControl.style["InActiveTabFontSize"]="14px";
            }
            if(props.selectedControl.style["TabLabelsJustified"]==="")
            {
              props.selectedControl.style["TabLabelsJustified"]='false' ;
            }

   
        }
        else if(value==='Style3')
        {             
                if(props.selectedControl.style["ActiveTabFontColor"]==="")
                {
                props.selectedControl.style["ActiveTabFontColor"]="#ffffff";
                }
                if(props.selectedControl.style["ActiveTabBackColor"]==="")
                {
                props.selectedControl.style["ActiveTabBackColor"]="#0072C6";
                }

                if(props.selectedControl.style["InActiveTabBackColor"]==="")
                {
                props.selectedControl.style["InActiveTabBackColor"]="#F8F8F8";
                }
                if(props.selectedControl.style["InActiveTabFontColor"]==="")
                {
                props.selectedControl.style["InActiveTabFontColor"]="#000000";
                }
                
                if(props.selectedControl.style["ActiveTabFontWeight"]==="")
                {
                props.selectedControl.style["ActiveTabFontWeight"]="500";
                }
                if(props.selectedControl.style["ActiveTabFontSize"]==="")
                {
                props.selectedControl.style["ActiveTabFontSize"]="14px";
                }
                if(props.selectedControl.style["ActiveTabFontFamily"]==="")
                {
                props.selectedControl.style["ActiveTabFontFamily"]="Open Sans";
                }           
    
                if(props.selectedControl.style["InActiveTabFontFamily"]==="")
                {
                props.selectedControl.style["InActiveTabFontFamily"]="Open Sans";
                }
                if(props.selectedControl.style["InActiveTabFontWeight"]==="")
                {
                props.selectedControl.style["InActiveTabFontWeight"]="600";
                }
                if(props.selectedControl.style["InActiveTabFontSize"]==="")
                {
                props.selectedControl.style["InActiveTabFontSize"]="14px";
                }
                if(props.selectedControl.style["TabLabelsJustified"]==="")
                {
                props.selectedControl.style["TabLabelsJustified"]='false' ;
                }
          
        }
        else if(value==='Style4')
        {             
                if(props.selectedControl.style["ActiveTabFontColor"]==="")
                {
                props.selectedControl.style["ActiveTabFontColor"]="#ffffff";  
                }
                if(props.selectedControl.style["ActiveTabBackColor"]==="")
                {
                props.selectedControl.style["ActiveTabBackColor"]="#0072C6";
                }
                if(props.selectedControl.style["ActiveTabFontWeight"]==="")
                {
                props.selectedControl.style["ActiveTabFontWeight"]="500";
                }
                if(props.selectedControl.style["ActiveTabFontSize"]==="")
                {
                props.selectedControl.style["ActiveTabFontSize"]="14px";
                }
                if(props.selectedControl.style["ActiveTabFontFamily"]==="")
                {
                props.selectedControl.style["ActiveTabFontFamily"]="Open Sans";
                }
                if(props.selectedControl.style["InActiveTabBackColor"]==="")
                {
                props.selectedControl.style["InActiveTabBackColor"]="#0072C6";
                }
                if(props.selectedControl.style["InActiveTabFontColor"]==="")
                {
                props.selectedControl.style["InActiveTabFontColor"]="#ffffff";
                }
                if(props.selectedControl.style["InActiveTabFontFamily"]==="")
                {
                props.selectedControl.style["InActiveTabFontFamily"]="Open Sans";
                }
                if(props.selectedControl.style["InActiveTabFontWeight"]==="")
                {
                props.selectedControl.style["InActiveTabFontWeight"]="500";
                }
                if(props.selectedControl.style["InActiveTabFontSize"]==="")
                {
                props.selectedControl.style["InActiveTabFontSize"]="14px";
                }
                if(props.selectedControl.style["TabLabelsJustified"]==="")
                {
                props.selectedControl.style["TabLabelsJustified"]='false' ;
                }
        }
   
        let temp_style = props.selectedControl.style;
        temp_style["ControlStyle"] = value;
        props.selectedControl.style["ControlStyle"] = value;    
        updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
    }

    htmlArray2.push(
        <ActiveIcon  passedValue={props.selectedControl.style["ControlStyle"]} activeStyle={{width:"84px"}} layout="3" inActiveStyle={{width:"84px"}} spacing="2px" handleStyleType={selectedOptionForStyles}>
                    <TabStyle1 value="Style1" label="Style1" />
                    <TabStyle2 value="Style2" label="Style2" />
                    <TabStyle3 value="Style3" label="Style3" />
                    <TabStyle4 value="Style4" label="Style4" />
        </ActiveIcon>
    );

    CollapsibleItems.push(<CollapsibleComponent id={'tabsLayout' + CollapsibleItems.length} heading="Tabs Layout" body={htmlArray2} setTopPx={setTopPx}/>);

    Object.keys(TabsPropertiesJson).forEach((key, index) => {
        var collapseComponentBody = [];
        Object.keys(TabsPropertiesJson[key]).forEach((property) => {
            if(property === "GetDataOnDemand") {
              if(!isPortal) {
                if(property!=='TabLabelsJustified' && property!== 'IconLabelAlignment' && property!=='ActiveTabCornerRadius' && property!=='BorderRadius')
                {   
                collapseComponentBody.push(prepareHtmlTag(property, ControlsMap[property].type));
                }else if(props.selectedControl.style["ControlStyle"]==="Style1" && property!="IconLabelAlignment" && property!=='ActiveTabCornerRadius' && property!=='BorderRadius')
                {
                   collapseComponentBody.push(prepareHtmlTag(property, ControlsMap[property].type));
                }else if(props.selectedControl.style["ControlStyle"]==="Style2" && property!="TabLabelsJustified"  && property!=='ActiveTabCornerRadius')
                {
                   collapseComponentBody.push(prepareHtmlTag(property, ControlsMap[property].type));
                }else if(props.selectedControl.style["ControlStyle"]==="Style3" && property!="TabLabelsJustified"  && property!=='IconLabelAlignment')
                {
                    collapseComponentBody.push(prepareHtmlTag(property, ControlsMap[property].type));
                }
              }
            }
            else {
              if(property!=='TabLabelsJustified' && property!== 'IconLabelAlignment' && property!=='ActiveTabCornerRadius' && property!=='BorderRadius')
                {   
                collapseComponentBody.push(prepareHtmlTag(property, ControlsMap[property].type));
                }else if(props.selectedControl.style["ControlStyle"]==="Style1" && property!="IconLabelAlignment" && property!=='ActiveTabCornerRadius' && property!=='BorderRadius')
                {
                   collapseComponentBody.push(prepareHtmlTag(property, ControlsMap[property].type));
                }else if(props.selectedControl.style["ControlStyle"]==="Style2" && property!="TabLabelsJustified"  && property!=='ActiveTabCornerRadius')
                {
                   collapseComponentBody.push(prepareHtmlTag(property, ControlsMap[property].type));
                }else if(props.selectedControl.style["ControlStyle"]==="Style3" && property!="TabLabelsJustified"  && property!=='IconLabelAlignment')
                {
                    collapseComponentBody.push(prepareHtmlTag(property, ControlsMap[property].type));
                }
            }
        })
        CollapsibleItems.push(<CollapsibleComponent id={index + ""} heading={key} body={collapseComponentBody} setTopPx={setTopPx}/>);
    })

    var htmlArray3 = [];

    htmlArray3.push(<div>
        <button className={styles.AddTabButton} id="addButtonCommonStyle" onClick={()=>setShowConditionalVisibility(!showConditionalVisibility)} onMouseOver={() => setIsButtonHover(true)} onMouseLeave={() => setIsButtonHover(false)}><PlusBlue color={isButtonHover ? "#FFFFFF" : "#0072c6"}/> &nbsp; &nbsp; {t(`translation:Set Rule`)}</button>
    </div>);

    CollapsibleItems.push(<CollapsibleComponent id={'tabProperty' + CollapsibleItems.length} heading="Conditional Visibility" body={htmlArray3}  setTopPx={setTopPx}/>);

    var htmlArray4 = [];

    htmlArray4.push(<div className={styles.CustomBlock} >
    </div>);

    function selectedOptionForTab(selectedControlId,selectedControlLabel,ControlLabelKey){
        var select = document.getElementById(selectedControlId+ControlLabelKey);
        var option_tag = select.options[select.selectedIndex];

        TabPropertyBackendJSON[ControlLabelKey]=option_tag.value;
                    
        let temp_style = props.selectedControl.style;
        temp_style[ControlLabelKey] = option_tag.value;
        props.selectedControl.style[ControlLabelKey] = option_tag.value;

          if(ControlLabelKey==='ActiveTabFontWeight'){
            if(option_tag.value==="Regular Italic"){
              props.selectedControl.style["ActiveTabWeight"] = 300;
              props.selectedControl.style["ActiveTabFontWeightStyle"] = "italic";
              props.selectedControl.style["ActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Semibold" || option_tag.value==="Medium"){
                props.selectedControl.style["ActiveTabWeight"] = 600;
                props.selectedControl.style["ActiveTabFontWeightStyle"] = "normal";
                props.selectedControl.style["ActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Bold"){
                props.selectedControl.style["ActiveTabWeight"] = "bold";
                props.selectedControl.style["ActiveTabFontWeightStyle"] = "normal";
                props.selectedControl.style["ActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Regular"){
                props.selectedControl.style["ActiveTabWeight"] = 300;
                props.selectedControl.style["ActiveTabFontWeightStyle"] = "normal";
                props.selectedControl.style["ActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Bold Italic"){
                props.selectedControl.style["ActiveTabWeight"] = "bold";
                props.selectedControl.style["ActiveTabFontWeightStyle"] = "italic";
                props.selectedControl.style["ActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Light Italic"){
                props.selectedControl.style["ActiveTabWeight"] = 100;
                props.selectedControl.style["ActiveTabFontWeightStyle"] = "italic";
                props.selectedControl.style["ActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Semibold Italic"){
                props.selectedControl.style["ActiveTabWeight"] = 600;
                props.selectedControl.style["ActiveTabFontWeightStyle"] = "italic";
                props.selectedControl.style["ActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Light"){
                props.selectedControl.style["ActiveTabWeight"] = 100;
                props.selectedControl.style["ActiveTabFontWeightStyle"] = "normal";
                props.selectedControl.style["ActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value === '' || option_tag.value === 'Select') {
              props.selectedControl.style["ActiveTabWeight"] = 300;
              props.selectedControl.style["ActiveTabFontWeightStyle"] = "normal";
              props.selectedControl.style["ActiveTabFontWeight"] = option_tag.value;
            }

        
          }else{

               if(option_tag.value==="Regular Italic"){
                props.selectedControl.style["InActiveTabWeight"] = 300;
                props.selectedControl.style["InActiveTabFontWeightStyle"] = "italic";
                props.selectedControl.style["InActiveTabFontWeight"] = option_tag.value;
               }
            else if(option_tag.value==="Semibold" || option_tag.value==="Medium"){
                props.selectedControl.style["InActiveTabWeight"] = 600;
                props.selectedControl.style["InActiveTabFontWeightStyle"] = "normal";
                props.selectedControl.style["InActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Bold"){
                props.selectedControl.style["InActiveTabWeight"] = "bold";
                props.selectedControl.style["InActiveTabFontWeightStyle"] = "normal";
                props.selectedControl.style["InActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Regular"){
                props.selectedControl.style["InActiveTabWeight"] = 300;
                props.selectedControl.style["InActiveTabFontWeightStyle"] = "normal";
                props.selectedControl.style["InActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Bold Italic"){
                props.selectedControl.style["InActiveTabWeight"] = "bold";
                props.selectedControl.style["InActiveTabFontWeightStyle"] = "italic";
                props.selectedControl.style["InActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Light Italic"){
                props.selectedControl.style["InActiveTabWeight"] = 100;
                props.selectedControl.style["InActiveTabFontWeightStyle"] = "italic";
                props.selectedControl.style["InActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Semibold Italic"){
                props.selectedControl.style["InActiveTabWeight"] = 600;
                props.selectedControl.style["InActiveTabFontWeightStyle"] = "italic";
                props.selectedControl.style["InActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value==="Light"){
                props.selectedControl.style["InActiveTabWeight"] = 100;
                props.selectedControl.style["InActiveTabFontWeightStyle"] = "normal";
                props.selectedControl.style["InActiveTabFontWeight"] = option_tag.value;
            }
            else if(option_tag.value === '' || option_tag.value === 'Select') {
              props.selectedControl.style["InActiveTabWeight"] = 300;
              props.selectedControl.style["InActiveTabFontWeightStyle"] = "normal";
              props.selectedControl.style["InActiveTabFontWeight"] = option_tag.value;
            }
          }
        updatedComponentInFormState(props.selectedControl.id , {...props.selectedControl})       
    }

    function handlePropertyForTab(propertyName, propertyValue,event, inputBoxId){
         
        try{
            if(event && (event.which !==8 && event.which !==46) && !(regexValidator(String.fromCharCode(event.which), ControlsMap[propertyName]["regexType"]))){
                event.preventDefault();
                setAlertData({...ControlsMap["showNotification"], 'open' : true, 'alertContentKey':'RegexErrorMessage',});

            }
            else {
                let temp_style = props.selectedControl.style;
                temp_style[propertyName] = propertyValue;
                props.selectedControl.style[propertyName] = propertyValue;    
                updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
            }
        } catch(e){
 
        } 
     }
    
    function handleToggle(e,field){
        const value = (e.current ? e.current.checked : e.target.value);
        props.selectedControl.style[field] = value+"";
        if(field === "Enable") {
          props.selectedControl.style["previousEnable"] = value;
        }
        if(field === "ReadOnlyStyle") {
          props.selectedControl.style["previousReadOnly"] = value;
        }
        if(field === "ReadOnly") {
          props.selectedControl.style["previousReadOnlyValue"] = value;
        }
        updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
    }

    function prepareHtmlTag(key, type) {

        switch (type.toLowerCase()) {
        case "combo":
        return (
          <>
            <div className={styles.parentDiv} style={translationStyles1?{display:"flex"}:null}>
              <span className={styles.leftLabel}>	{t(`translation:${ControlsMap[key].label}`)} </span>
              <div className={styles.RightLabelSelectForText}>
              { (key!== "ActiveTabFontWeight" && key!== "InActiveTabFontWeight") ?
              		  <>
                <select
                  id={props.selectedControl.id + key}
                  className={styles.RightLabelSelectForText}
                  onChange={() =>
                    selectedOptionForTab(
                      props.selectedControl.id,
                      ControlsMap[key].label,
                      key
                    )
                  }
                >
                  {ControlsMap[key].children.map(
                    (optionTagData, optionCount) => {
                      if (ControlsMap[key].children[optionCount].Value || ControlsMap[key].children[optionCount].Value === "" || ControlsMap[key].children[optionCount].Value === "Select") {
                        return (
                          <>
                            {ControlsMap[key].children[optionCount].Value ===
                            props.selectedControl.style[key] ? (
                              <option
                              style={ControlsMap[key].children[optionCount]?.style}
                                value={
                                  ControlsMap[key].children[optionCount].Value
                                }
                                selected
                              >                                
                                {t(`translation:${ControlsMap[key].children[optionCount].Title}`)}
                              </option>
                            ) : (
                              <option
                              style={ControlsMap[key].children[optionCount]?.style}
                                value={
                                  ControlsMap[key].children[optionCount].Value
                                }
                              >
                                {t(`translation:${ControlsMap[key].children[optionCount].Title}`)}
                                
                              </option>
                            )}
                          </>
                        );
                      }
                    }
                  )}
                </select>
                </>
                  :
                  key === "ActiveTabFontWeight" ?
                  <>
                     <select id={props.selectedControl.id + key} className={styles.RightLabelSelectForText}
                  onChange={() =>selectedOptionForTab(props.selectedControl.id, ControlsMap[key].label, key)}
                >
                 {props.selectedControl.style && props.selectedControl.style["ActiveTabFontFamily"]
                   ?
                   <>
                 {FontWeightListJson[props.selectedControl.style["ActiveTabFontFamily"]].children.map(
                  (optionTagData, optionCount) => {
                    if (FontWeightListJson[props.selectedControl.style["ActiveTabFontFamily"]].children[optionCount].Value || FontWeightListJson[props.selectedControl.style["ActiveTabFontFamily"]].children[optionCount].Value === "Select" || FontWeightListJson[props.selectedControl.style["ActiveTabFontFamily"]].children[optionCount].Value === "") {
                    return (
                      <>
                      {FontWeightListJson[props.selectedControl.style["ActiveTabFontFamily"]].children[optionCount].Value ===
                      props.selectedControl.style[key] ? (
                        <option
                        value={
                          FontWeightListJson[props.selectedControl.style["ActiveTabFontFamily"]].children[optionCount].Value
                        }
                        selected
                        >
                          {t(`translation:${FontWeightListJson[props.selectedControl.style["ActiveTabFontFamily"]].children[optionCount].Title}`)}
                        
                        </option>
                      ) : (
                        <option
                        value={
                          FontWeightListJson[props.selectedControl.style["ActiveTabFontFamily"]].children[optionCount].Value
                        }
                        >
                        {t(`translation:${FontWeightListJson[props.selectedControl.style["ActiveTabFontFamily"]].children[optionCount].Title}`)}
                        </option>
                      )}
                      </>
                    );
                    }
                  }
                  )}
                  </>
                  :
                  
                  <>
                  {FontWeightListJson["Open Sans"].children.map(
                 (optionTagData, optionCount) => {
                   if (FontWeightListJson["Open Sans"].children[optionCount].Value || FontWeightListJson["Open Sans"].children[optionCount].Value === "" || FontWeightListJson["Open Sans"].children[optionCount].Value === "Select") {
                   return (
                     <>
                     {FontWeightListJson["Open Sans"].children[optionCount].Value ===
                     props.selectedControl.style[key] ? (
                       <option
                       value={
                         FontWeightListJson["Open Sans"].children[optionCount].Value
                       }
                       selected
                       >
                        {t(`translation:${FontWeightListJson["Open Sans"].children[optionCount].Title}`)}                       
                       </option>
                     ) : (
                       <option
                       value={
                         FontWeightListJson["Open Sans"].children[optionCount].Value
                       }
                       >
                        {t(`translation:${FontWeightListJson["Open Sans"].children[optionCount].Title}`)}
                       
                       </option>
                     )}
                     </>
                   );
                   }
                 }
                 )}
                 </>
                  
                  
                  }
           
                </select>
                  </>
                          :
                  <>
                  <select id={props.selectedControl.id + key} className={styles.RightLabelSelectForText}
                  onChange={() =>selectedOptionForTab(props.selectedControl.id, ControlsMap[key].label, key)}
                >
                 {props.selectedControl.style && props.selectedControl.style["InActiveTabFontFamily"]
                   ?
                   <>
                 {FontWeightListJson[props.selectedControl.style["InActiveTabFontFamily"]].children.map(
                  (optionTagData, optionCount) => {
                    if (FontWeightListJson[props.selectedControl.style["InActiveTabFontFamily"]].children[optionCount].Value || FontWeightListJson[props.selectedControl.style["InActiveTabFontFamily"]].children[optionCount].Value === "" || FontWeightListJson[props.selectedControl.style["InActiveTabFontFamily"]].children[optionCount].Value === "Select") {
                    return (
                      <>
                      {FontWeightListJson[props.selectedControl.style["InActiveTabFontFamily"]].children[optionCount].Value ===
                      props.selectedControl.style[key] ? (
                        <option
                        value={
                          FontWeightListJson[props.selectedControl.style["InActiveTabFontFamily"]].children[optionCount].Value
                        }
                        selected
                        >
                        {t(`translation:${FontWeightListJson[props.selectedControl.style["InActiveTabFontFamily"]].children[optionCount].Title}`)}
                        
                        </option>
                      ) : (
                        <option
                        value={
                          FontWeightListJson[props.selectedControl.style["InActiveTabFontFamily"]].children[optionCount].Value
                        }
                        >
                        {t(`translation:${FontWeightListJson[props.selectedControl.style["InActiveTabFontFamily"]].children[optionCount].Title}`)}                     
                        
                        </option>
                      )}
                      </>
                    );
                    }
                  }
                  )}
                  </>
                  :
                  
                  <>
                  {FontWeightListJson["Open Sans"].children.map(
                 (optionTagData, optionCount) => {
                   if (FontWeightListJson["Open Sans"].children[optionCount].Value || FontWeightListJson["Open Sans"].children[optionCount].Value === "" || FontWeightListJson["Open Sans"].children[optionCount].Value === "Select") {
                   return (
                     <>
                     {FontWeightListJson["Open Sans"].children[optionCount].Value ===
                     props.selectedControl.style[key] ? (
                       <option
                       value={
                         FontWeightListJson["Open Sans"].children[optionCount].Value
                       }
                       selected
                       >
                        {t(`translation:${FontWeightListJson["Open Sans"].children[optionCount].Title}`)}                     
                        
                       
                       </option>
                     ) : (
                       <option
                       value={
                         FontWeightListJson["Open Sans"].children[optionCount].Value
                       }
                       >
                        {t(`translation:${FontWeightListJson["Open Sans"].children[optionCount].Title}`)}
                       
                       </option>
                     )}
                     </>
                   );
                   }
                 }
                 )}
                 </>
                  
                  
                  }
           
                </select>
                 </>
                 }
              </div>
            </div>
          </>
        );

       case "colorpicker" : return (
                <div className="colorpicker">
                {t(`translation:${ControlsMap[key].label}`)}
                <ColorPicker  color={props.selectedControl.style[key]} handleProperty={handlePropertyForTab} propertyName={key} />
                </div>
            )



        case "toggle" : return(
            <div className={styles.parentDiv} style={translationStyles1?{display:"flex"}:null}>
                    <span className={styles.leftLabel} >{t(`translation:${ControlsMap[key].label}`)}</span>
                    <div className={translationStyles1 ? styles.toggleCheckboxArabic : styles.toggleCheckbox}>
                    <ToggleBtn 
                        toggleState={props.selectedControl.style[key]} 
                        handleToggle={handleToggle}
                        field={key}
                        id={props?.selectedControl?.style?.CustomId ? props?.selectedControl?.style?.CustomId : props?.selectedControl?.id}
                        />
                    </div>
                </div>
            )
        }
    }
  
    return (
        <>
        <ControlLabel label="Tab Container" handleDelete={deleteControl} id={props.selectedControl.id} />
        <div className="propertiesBody tabProperties">
            <Card.Body className={styles.cardBodyTabProperties}>
                {
                    CollapsibleItems.map((item, index) => {
                        return item;
                    })
                }
            </Card.Body>  
            </div>
        { showConditionalVisibility === true ?
            <ConditionalVisibilityTabs setShowConditionalVisibility={()=> setShowConditionalVisibility(!showConditionalVisibility)}
            ruleDetails={props.selectedControl && props.selectedControl.RuleDetails ? props.selectedControl.RuleDetails : { } }
            selectedControl={props.selectedControl}  formState2={formState2}  portalState={(microFrontendData?.formPageType || microFrontendData?.processName )?'Processes':"Portals"}
            updatedComponentInFormState={updatedComponentInFormState}
            processId={processId}
            setCheckConditionalitySave={() => setCheckConditionalitySave(!checkConditionalitySave)}
            statusType={statusType}
            />
            :<></>
        }    
        {alertData.open === true &&
        <CustomizedAlert alertData={alertData} handleFunction={(sheetId,sheetIndex)=>deleteSheet(sheetId,sheetIndex)}/>
        }
        </>
    )

}


export default TabsProperties;